.edit-board {
  display: flex;
}

textarea {
  border-radius: .3rem;
  width: 50%;
  min-height: 50em;
  overflow: hidden auto;
}

pre {
  white-space: pre-wrap;
  background-color: #f0f8ff;
  border-radius: .5rem;
  width: 50%;
  min-height: 50em;
  margin-top: 0;
  margin-bottom: 0;
  position: relative;
  overflow-y: auto;
}

code {
  font-family: monospace;
  font-size: 16px;
}

pre code {
  white-space: pre;
  -webkit-overflow-scrolling: touch;
  min-width: 100px;
  max-width: 100%;
  display: block;
  overflow-x: scroll;
}

pre button {
  top: 15px;
  right: 15px;
  position: absolute;
  inset: 5px 5px auto auto;
  box-shadow: -3px 3px #999;
}

pre button:hover {
  cursor: pointer;
  background-color: #a9a9a9;
}

h1, p {
  text-align: center;
